<template>
    <div>
        <Head title="" @close="close"/>
        <div class="pages-body">
            <MSelect type="speaker" @medioId="openDevice" :tips="tips" title="" @clean="cleanStream"/>
            <div class="headset-box audio-box">
                <p id="tips" style="display:none"></p>
                <p id="loading-tips" style="display:none;">...</p>
                <div id="visualizer_wrapper">
                    <canvas id="canvas" width="600" height="150"></canvas>
                </div>
            </div>

            <div id="next_area" v-if="is_show">
                <div class="tips red">
                    <p></p>
                </div>
                <div class="btn-area">
                    <a class="btn btn-success" href="javascript:;" @click="onBtnNext('1')"></a>
                    <a class="btn btn-fail" href="javascript:;" @click="onBtnNext('0')"></a>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
  import Head from './head'
  import Visualizer from '../script/headphones'
  import MSelect from './select'

  const speakerObj = new Visualizer()
  export default {
    props: {
      config: {
        type: Object
      }
    },
    components: {
      Head,
      MSelect
    },
    data () {
      return {
        tips: '',
        is_show: false
      }
    },
    mounted () {
      setTimeout(() => {
        this.is_show = true
      }, 3000)
    },
    methods: {
      // 
      cleanStream () {
        speakerObj._audioEnd(() => {
          console.log('clean mp3 audio')
        })
      },
      // 
      onBtnNext (val) {
        this.$emit('setReport', {type: 'speaker', value: val})
        speakerObj._audioEnd(() => {
          this.$emit('next', 'reportCom')
        })
      },
      openDevice (devices) {
        let opt = {
          audio: devices === 'default' ? true : {deviceId: devices}
        }
        speakerObj.init(this.config)
        navigator.mediaDevices.getUserMedia(opt).then(() => {
          console.log('open speaker')
        }).catch((err) => {
          console.log(err.name)
          if (err.name === 'NotAllowedError') {
            this.tips = ''
            console.log(this.tips)
          } else {
            this.tips = ''
          }
        })
      },
      close () {
        speakerObj._audioEnd(() => {
          this.$emit('close')
        })
      }
    }
  }
</script>
<style lang="scss">
    #visualizer_wrapper > #canvas {
        background-color: #042044;
        padding: 10px;
        border-radius: 10px;
        border: 1px solid #010f21;
        margin: 23px 0;
    }

</style>
